import React from "react";
import debounceRender from 'react-debounce-render';
import { useForm } from "react-hook-form";

/*
 * @author Blake Vente
 */

// styles
const marginPad = {
  margin: "10px",
  padding: "10px",
}

const inputField = {
  width: "50%",
  fontSize: "1rem",
  borderRadius: "10px",
  color: "var(--theme-ui-colors-text)",
  borderColor: "var(--theme-ui-colors-text)",
  borderStyle: "solid",
  borderWidth: "1px",
  backgroundColor: "var(--theme-ui-colors-transparent,transparent)",
  display: "block",
  ...marginPad
}

const fillContainer = {
  backgroundColor: "white",
  width: "276px", // 256 + 2 * padding
  ...marginPad
}


function ContactQRCode(props) {
  console.log(props)
  if (!props.transcript) {
    return <span>No transcript</span>
  }
  const wordItems = JSON.parse(props.transcript)?.results?.items.slice(0, 10) || [];

  return (
    <div >
      {wordItems.map(({ start_time, end_time, alternatives }) => <div>{start_time} {end_time} {alternatives}</div>)}
    </div>
  )
}

const DbounceContactQRCode = debounceRender(ContactQRCode, 500);

export default function QRContact() {

  const formPlaceholders = {
    textgrid: "{}",
    transcript: '{}',
  };

  // same keys but with the values cleared
  const formdefaultValues = formPlaceholders;

  const { register, watch } = useForm(
    { defaultValues: formdefaultValues }
  );

  return (
    <div>
      <form>
        {
          Object.keys(formPlaceholders).map(
            (registerKey, i) =>
              <input key={i}
                style={inputField}
                placeholder={formPlaceholders[registerKey]}
                {...register(registerKey)}
              />
          )}
      </form>
      <DbounceContactQRCode {...watch()} />
    </div>
  );
}
